body {
  margin: 0;
  padding: 0;
  font-family: 'Georgia', Courier, monospace;
  background-color: #e3f4f1;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}


.container {
  width: 100%;
  max-width: 600px; 
  height: 95vh; 
  width: 300mm; 
  background-color: #f0f8ff; /* Color de fondo */
  display: flex;
  flex-direction: column;
  justify-content: center; 
  align-items: center; 
  padding: 20px;
  box-sizing: border-box;
  border-radius: 25px;
}

h1 {
  color: #333;
  font-size: 2.5rem;
  margin-bottom: 20px;
  font-weight: bold;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 600px; 
}

label {
  font-size: 1.2rem;
  margin-top: 20px;
  color: #666;
}

input, select {
  width: 70%; 
  padding: 10px;
  margin-top: 10px; 
  border: 2px solid #b2d8d8; 
  border-radius: 10px; 
  font-size: 1rem; 
  box-sizing: border-box; 
  background-color: #f8f8f8; 
  color: #333; 
  transition: all 0.3s ease; 
}

input:focus, select:focus {
  outline: none; 
  border-color: #00796b; 
  box-shadow: 0 0 10px rgba(0, 121, 107, 0.3); 
}

input:hover, select:hover {
  border-color: #009688; 
}

input::placeholder {
  color: #999; 
  font-style: italic; 
}

button {
  background-color: #333;
  color: white;
  padding: 12px 30px;
  margin-top: 20px;
  border: none;
  border-radius: 5px;
  font-size: 1.2rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #555;
}

/* Responsive */

/* para tablets */
@media screen and (max-width: 768px) {
  h1 {
    font-size: 2rem; /* Reduce font size for tablets */
  }

  input, select {
    font-size: 0.9rem;
  }

  button {
    font-size: 1.1rem;
  }
}

/* Para telefono */
@media screen and (max-width: 480px) {
  h1 {
    font-size: 1.5rem;
  }

  label {
    font-size: 1rem;
  }

  input, select {
    font-size: 0.8rem;
  }

  button {
    padding: 10px 20px;
    font-size: 1rem;
  }
}
